import React, { useState } from 'react';
import { ClubDialog, ClubTable } from '../../components/features/clubs';
import type { Club } from '../../types';

const ClubConfig: React.FC = () => {
  const [tableVersion, setTableVersion] = useState(1);
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogClubUuid, setDialogClubUuid] = useState('');

  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  const onCreate = () => {
    setDialogClubUuid('');
    setDialogVisible(true);
  };

  const onEdit = (row: Club) => {
    setDialogClubUuid(row.uuid || '');
    setDialogVisible(true);
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogClubUuid('');
  };

  const handleCreated = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    onRefresh();
    handleDialogClose();
  };

  return (
    <div>
      <ClubTable
        refreshVersion={tableVersion}
        onCreate={onCreate}
        onEdit={onEdit}
      />
      <ClubDialog
        visible={dialogVisible}
        clubUuid={dialogClubUuid}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />
    </div>
  );
};

export default ClubConfig;
