import { Card, Empty, message } from 'antd';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  MeetingCalendar,
  MeetingDialog,
  MeetingTable,
} from '../../components/features/meetings';
import { usePreference } from '../../hooks';
import type { Meeting } from '../../types';

const MeetingSchedule: React.FC = () => {
  const { club: preferenceClub, setMeeting } = usePreference();
  const navigate = useNavigate();

  // 表格状态管理
  const [tableVersion, setTableVersion] = useState(1);
  const [tableVisible, setTableVisible] = useState(false); // 默认显示日历视图

  // 对话框状态管理
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogMeetingUUID, setDialogMeetingUUID] = useState('');

  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  const onCreate = () => {
    setDialogMeetingUUID('');
    setDialogVisible(true);
  };

  const onEdit = (row: Meeting) => {
    setDialogMeetingUUID(row.uuid || '');
    setDialogVisible(true);
  };

  const onSelect = (meeting: Meeting) => {
    if (meeting.uuid) {
      // 设置选中的会议到偏好设置
      setMeeting(meeting.uuid);
      // 跳转到会议管理页面
      navigate('/meetings/management');
      // 显示成功消息
      message.success(`已选择会议"${meeting.name}"并跳转到会议管理页面`);
    }
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogMeetingUUID('');
  };

  const handleCreated = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleChangeFormat = () => {
    setTableVisible(prev => !prev);
  };

  // 如果没有选择俱乐部，显示空状态
  if (!preferenceClub) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择俱乐部" />
      </Card>
    );
  }

  return (
    <div>
      {tableVisible ? (
        <MeetingTable
          refreshVersion={tableVersion}
          clubUuid={preferenceClub}
          onCreate={onCreate}
          onEdit={onEdit}
          onSelect={onSelect}
          onChangeFormat={handleChangeFormat}
        />
      ) : (
        <MeetingCalendar
          refreshVersion={tableVersion}
          clubUuid={preferenceClub}
          onCreate={onCreate}
          onEdit={onEdit}
          onSelect={onSelect}
          onChangeFormat={handleChangeFormat}
        />
      )}

      <MeetingDialog
        visible={dialogVisible}
        clubUuid={preferenceClub}
        meetingUuid={dialogMeetingUUID}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />
    </div>
  );
};

export default MeetingSchedule;
