import { Card, Empty } from 'antd';
import React, { useState } from 'react';
import {
  MeetingRoleManagement,
  MeetingSessionManagement,
} from '../../components/features/meetings';
import { usePreference } from '../../hooks';

const MeetingManagement: React.FC = () => {
  const { club: preferenceClub, meeting: preferenceMeeting } = usePreference();

  // 控制显示会议环节还是会议角色
  const [showMeetingSession, setShowMeetingSession] = useState(true);

  // 如果没有选择俱乐部，显示空状态
  if (!preferenceClub) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择俱乐部" />
      </Card>
    );
  }

  // 如果没有选择会议，显示空状态
  if (!preferenceMeeting) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择会议" />
      </Card>
    );
  }

  const handleChangeFormat = () => {
    setShowMeetingSession(prev => !prev);
  };

  return (
    <div>
      {/* 内容区域 */}
      {showMeetingSession ? (
        <MeetingSessionManagement
          clubUuid={preferenceClub}
          meetingUuid={preferenceMeeting}
          onChangeFormat={handleChangeFormat}
        />
      ) : (
        <MeetingRoleManagement
          clubUuid={preferenceClub}
          meetingUuid={preferenceMeeting}
          onChangeFormat={handleChangeFormat}
        />
      )}
    </div>
  );
};

export default MeetingManagement;
