import { Card, Empty } from 'antd';
import React, { useState } from 'react';
import {
  MemberDialog,
  MemberTable,
} from '../../components/features/participants';
import { usePreference } from '../../hooks';
import type { Member } from '../../types';

const MemberManagement: React.FC = () => {
  const { club: preferenceClub } = usePreference();

  // 表格状态管理
  const [tableVersion, setTableVersion] = useState(1);

  // 对话框状态管理
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogMemberUUID, setDialogMemberUUID] = useState('');

  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  const onCreate = () => {
    setDialogMemberUUID('');
    setDialogVisible(true);
  };

  const onEdit = (row: Member) => {
    setDialogMemberUUID(row.uuid || '');
    setDialogVisible(true);
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogMemberUUID('');
  };

  const handleCreated = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    onRefresh();
    handleDialogClose();
  };

  // 如果没有选择俱乐部，显示空状态
  if (!preferenceClub) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择俱乐部" />
      </Card>
    );
  }

  return (
    <div>
      <MemberTable
        refreshVersion={tableVersion}
        clubUuid={preferenceClub}
        onCreate={onCreate}
        onEdit={onEdit}
        onRefresh={onRefresh}
      />
      <MemberDialog
        visible={dialogVisible}
        clubUuid={preferenceClub}
        memberUuid={dialogMemberUUID}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />
    </div>
  );
};

export default MemberManagement;
