import React, { useState } from 'react';
import { ProgramDialog, ProgramTable } from '../../components/features/clubs';
import type { Program } from '../../types';

const ProgramConfig: React.FC = () => {
  const [tableVersion, setTableVersion] = useState(1);
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogProgramUUID, setDialogProgramUUID] = useState('');

  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  const onCreate = () => {
    setDialogProgramUUID('');
    setDialogVisible(true);
  };

  const onEdit = (row: Program) => {
    setDialogProgramUUID(row.uuid || '');
    setDialogVisible(true);
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogProgramUUID('');
  };

  const handleCreated = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    onRefresh();
    handleDialogClose();
  };

  return (
    <div>
      <ProgramTable
        refreshVersion={tableVersion}
        onCreate={onCreate}
        onEdit={onEdit}
      />
      <ProgramDialog
        visible={dialogVisible}
        programUuid={dialogProgramUUID}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />
    </div>
  );
};

export default ProgramConfig;
