import { Card, Empty, Modal } from 'antd';
import React, { useState } from 'react';
import {
  ParticipantDialog,
  ParticipantTable,
  ParticipantTitleDialog,
  ParticipantTitleTable,
} from '../../components/features/participants';
import { usePreference } from '../../hooks';
import type { Participant, ParticipantTitle } from '../../types';

const ParticipantManagement: React.FC = () => {
  const { club: preferenceClub } = usePreference();

  // 参与者表格状态管理
  const [participantTableVersion, setParticipantTableVersion] = useState(1);
  const [participantDialogVisible, setParticipantDialogVisible] =
    useState(false);
  const [participantUUID, setParticipantUUID] = useState('');

  // 参与者头衔状态管理
  const [titleTableVisible, setTitleTableVisible] = useState(false);
  const [titleTableVersion, setTitleTableVersion] = useState(1);
  const [titleDialogVisible, setTitleDialogVisible] = useState(false);
  const [titleUUID, setTitleUUID] = useState('');

  // 参与者相关操作
  const handleParticipantRefresh = () => {
    setParticipantTableVersion(prev => prev + 1);
  };

  const handleParticipantCreate = () => {
    setParticipantUUID('');
    setParticipantDialogVisible(true);
  };

  const handleParticipantEdit = (participant: Participant) => {
    setParticipantUUID(participant.uuid || '');
    setParticipantDialogVisible(true);
  };

  const handleParticipantShowTitle = (participant: Participant) => {
    setParticipantUUID(participant.uuid || '');
    setTitleTableVisible(true);
  };

  const handleParticipantDialogClose = () => {
    setParticipantDialogVisible(false);
    setParticipantUUID('');
  };

  const handleParticipantCreated = () => {
    handleParticipantRefresh();
    handleParticipantDialogClose();
  };

  const handleParticipantEdited = () => {
    handleParticipantRefresh();
    handleParticipantDialogClose();
  };

  // 参与者头衔相关操作
  const handleTitleRefresh = () => {
    setTitleTableVersion(prev => prev + 1);
    setParticipantTableVersion(prev => prev + 1); // 同时刷新参与者表格以更新头衔信息
  };

  const handleTitleCreate = () => {
    setTitleUUID('');
    setTitleDialogVisible(true);
  };

  const handleTitleEdit = (title: ParticipantTitle) => {
    setTitleUUID(title.uuid || '');
    setTitleDialogVisible(true);
  };

  const handleTitleTableClose = () => {
    setTitleTableVisible(false);
    setParticipantUUID('');
  };

  const handleTitleDialogClose = () => {
    setTitleDialogVisible(false);
    setTitleUUID('');
  };

  const handleTitleCreated = () => {
    handleTitleRefresh();
    handleTitleDialogClose();
  };

  const handleTitleEdited = () => {
    handleTitleRefresh();
    handleTitleDialogClose();
  };

  // 如果没有选择俱乐部，显示空状态
  if (!preferenceClub) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择俱乐部" />
      </Card>
    );
  }

  return (
    <>
      <ParticipantTable
        refreshVersion={participantTableVersion}
        clubUuid={preferenceClub}
        onCreate={handleParticipantCreate}
        onEdit={handleParticipantEdit}
        onShowTitle={handleParticipantShowTitle}
      />

      <ParticipantDialog
        visible={participantDialogVisible}
        clubUuid={preferenceClub}
        participantUuid={participantUUID}
        onClose={handleParticipantDialogClose}
        onCreated={handleParticipantCreated}
        onEdited={handleParticipantEdited}
      />

      <Modal
        title="参与者头衔管理"
        open={titleTableVisible}
        onCancel={handleTitleTableClose}
        footer={null}
        width="80%"
        destroyOnHidden
      >
        {titleTableVisible && (
          <ParticipantTitleTable
            refreshVersion={titleTableVersion}
            participantUuid={participantUUID}
            onCreate={handleTitleCreate}
            onEdit={handleTitleEdit}
            onDeleted={handleTitleRefresh}
          />
        )}
      </Modal>

      <ParticipantTitleDialog
        visible={titleDialogVisible}
        participantUuid={participantUUID}
        participantTitleUuid={titleUUID}
        onClose={handleTitleDialogClose}
        onCreated={handleTitleCreated}
        onEdited={handleTitleEdited}
      />
    </>
  );
};

export default ParticipantManagement;
