import { Modal, message } from 'antd';
import React, { useState } from 'react';
import {
  TransactionDialog,
  TransactionStatistic,
  TransactionTable,
} from '../../components/features/accounts';
import { usePreference } from '../../hooks';
import { transactionsApi } from '../../services';
import type { Transaction } from '../../types';

const TransactionManagement: React.FC = () => {
  // 使用偏好设置管理
  const {
    club: preferenceClub,
    account: preferenceAccount,
    setAccount,
  } = usePreference();

  // 表格状态管理
  const [tableVersion, setTableVersion] = useState(1);

  // 对话框状态管理
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogTransactionUUID, setDialogTransactionUUID] = useState('');

  // 统计对话框状态管理
  const [dialogStatisticVisible, setDialogStatisticVisible] = useState(false);
  const [dialogStatisticParams, setDialogStatisticParams] = useState<any>({});

  // 账户选择变化处理
  const handleAccountChange = (accountUuid: string) => {
    setAccount(accountUuid);
    onRefresh(); // 账户变化时刷新表格
  };

  // 刷新表格数据
  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  // 创建交易记录
  const onCreate = () => {
    setDialogTransactionUUID('');
    setDialogVisible(true);
  };

  // 编辑交易记录
  const onEdit = (row: Transaction) => {
    setDialogTransactionUUID(row.uuid || '');
    setDialogVisible(true);
  };

  // 导出交易记录
  const onExport = (params: any) => {
    try {
      const urlParams = {
        ...params,
        transact_time_zone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      };

      // 使用API获取导出URL
      const url = transactionsApi.getTransactionExportUri(urlParams);
      window.open(url, '_blank')?.focus();

      message.success('导出成功');
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('导出失败:', error);
      message.error('导出失败');
    }
  };

  // 显示统计信息
  const onShowStatistic = (params: any) => {
    setDialogStatisticParams(params);
    setDialogStatisticVisible(true);
  };

  // 交易记录创建成功
  const handleCreated = () => {
    setDialogVisible(false);
    onRefresh();
    message.success('交易记录创建成功');
  };

  // 交易记录编辑成功
  const handleEdited = () => {
    setDialogVisible(false);
    onRefresh();
    message.success('交易记录更新成功');
  };

  // 关闭交易记录对话框
  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogTransactionUUID('');
  };

  // 关闭统计对话框
  const handleStatisticClose = () => {
    setDialogStatisticVisible(false);
    setDialogStatisticParams({});
  };

  return (
    <>
      {/* 交易记录表格 */}
      <TransactionTable
        refreshVersion={tableVersion}
        clubUuid={preferenceClub}
        accountUuid={preferenceAccount || undefined}
        onAccountChange={handleAccountChange}
        onCreate={onCreate}
        onEdit={onEdit}
        onExport={onExport}
        onShowStatistic={onShowStatistic}
      />

      {/* 交易记录对话框 */}
      <TransactionDialog
        visible={dialogVisible}
        clubUuid={preferenceClub}
        accountUuid={preferenceAccount}
        transactionUuid={dialogTransactionUUID}
        onCreated={handleCreated}
        onEdited={handleEdited}
        onClose={handleDialogClose}
      />

      {/* 统计对话框 */}
      <Modal
        title="交易统计"
        open={dialogStatisticVisible}
        onCancel={handleStatisticClose}
        footer={null}
        width="80%"
        destroyOnHidden
      >
        <TransactionStatistic
          clubUuid={preferenceClub}
          accountUuid={preferenceAccount}
          extraParams={dialogStatisticParams}
        />
      </Modal>
    </>
  );
};

export default TransactionManagement;
