import { Modal } from 'antd';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  AccountDialog,
  AccountTable,
  TransactionStatistic,
} from '../../components/features/accounts';
import { usePreference } from '../../hooks';
import type { Account } from '../../types';

const AccountManagement: React.FC = () => {
  const { club: selectedClubUuid, setAccount } = usePreference();
  const navigate = useNavigate();

  const [refreshVersion, setRefreshVersion] = useState(1);
  const [dialogVisible, setDialogVisible] = useState(false);
  const [statisticVisible, setStatisticVisible] = useState(false);
  const [dialogAccountUuid, setDialogAccountUuid] = useState('');
  const [statisticTitle, setStatisticTitle] = useState('');

  const handleRefresh = () => {
    setRefreshVersion(prev => prev + 1);
  };

  const handleCreate = () => {
    setDialogAccountUuid('');
    setDialogVisible(true);
  };

  const handleEdit = (account: Account) => {
    setDialogAccountUuid(account.uuid || '');
    setDialogVisible(true);
  };

  const handleSelect = (account: Account) => {
    // 设置选中的账户到用户偏好并跳转到交易页面
    if (account.uuid) {
      setAccount(account.uuid);
      navigate('/accounting/transaction');
    }
  };

  const handleShowStatistic = (account: Account) => {
    setDialogAccountUuid(account.uuid || '');
    setStatisticTitle(account.name);
    setStatisticVisible(true);
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogAccountUuid('');
  };

  const handleStatisticClose = () => {
    setStatisticVisible(false);
    setDialogAccountUuid('');
    setStatisticTitle('');
  };

  const handleCreated = () => {
    handleRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    handleRefresh();
    handleDialogClose();
  };

  return (
    <>
      <AccountTable
        refreshVersion={refreshVersion}
        clubUuid={selectedClubUuid}
        onCreate={handleCreate}
        onEdit={handleEdit}
        onSelect={handleSelect}
        onShowStatistic={handleShowStatistic}
      />

      <AccountDialog
        visible={dialogVisible}
        clubUuid={selectedClubUuid}
        accountUuid={dialogAccountUuid}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />

      <Modal
        title={`${statisticTitle} - 交易统计`}
        open={statisticVisible}
        onCancel={handleStatisticClose}
        footer={null}
        width="80%"
        destroyOnHidden
      >
        <TransactionStatistic
          clubUuid={selectedClubUuid}
          accountUuid={dialogAccountUuid}
        />
      </Modal>
    </>
  );
};

export default AccountManagement;
