import { Card, Empty } from 'antd';
import React, { useState } from 'react';
import { VenueDialog, VenueTable } from '../../components/features/meetings';
import { usePreference } from '../../hooks';
import type { Venue } from '../../types';

const VenueManagement: React.FC = () => {
  const { club: preferenceClub } = usePreference();

  // 表格状态管理
  const [tableVersion, setTableVersion] = useState(1);

  // 对话框状态管理
  const [dialogVisible, setDialogVisible] = useState(false);
  const [dialogVenueUUID, setDialogVenueUUID] = useState('');

  const onRefresh = () => {
    setTableVersion(prev => prev + 1);
  };

  const onCreate = () => {
    setDialogVenueUUID('');
    setDialogVisible(true);
  };

  const onEdit = (row: Venue) => {
    setDialogVenueUUID(row.uuid || '');
    setDialogVisible(true);
  };

  const handleDialogClose = () => {
    setDialogVisible(false);
    setDialogVenueUUID('');
  };

  const handleCreated = () => {
    onRefresh();
    handleDialogClose();
  };

  const handleEdited = () => {
    onRefresh();
    handleDialogClose();
  };

  // 如果没有选择俱乐部，显示空状态
  if (!preferenceClub) {
    return (
      <Card className="tm-brand-card">
        <Empty description="请先选择俱乐部" />
      </Card>
    );
  }

  return (
    <div>
      <VenueTable
        refreshVersion={tableVersion}
        clubUuid={preferenceClub}
        onCreate={onCreate}
        onEdit={onEdit}
      />
      <VenueDialog
        visible={dialogVisible}
        clubUuid={preferenceClub}
        venueUuid={dialogVenueUUID}
        onClose={handleDialogClose}
        onCreated={handleCreated}
        onEdited={handleEdited}
      />
    </div>
  );
};

export default VenueManagement;
